body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  overflow: hidden;
}

.header {
  padding: 10px;
  background-color: orange;
  color: #fff;
  position: fixed;
  right:0;
  top: 0;
  left: 0;
  z-index: 100;
}

.header h1 {
  font-size: 25px;
  padding: 0;
  margin: 0;
  text-align: center;

}

.map {
  width: 100vw;
  height: 100vh;
  position: relative;
}

.location-icon {
  font-size: 2rem;
  color: orange;
}

.loader {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh
}

.loader img {
  width: 500px;
}

.loader h1 {
  margin-top: -100px;
}


.location-info {
  position: absolute;
  top: 100px;
  right: 50px;
  width: 400px;
  min-height: 200px;
  padding: 15px;
  background-color: rgb(0,0,0,0.8);
  border-radius: 10px;
  font-size: 18px;
  color: white;
}

.location-info ul{
  list-style-type:none;
  padding: 0;
}

.location-info li{
  padding: 5px 0;
  max-width: 500px;
  font-size: 15px;
}